@import '../../../colors';
@import '../../../mixins';

.form {
    &__container {
        background: $app-boxes;
        width: 60%;
        padding: 6rem 4rem;
        margin: auto;
        border-radius: 1rem;
        -webkit-border-radius: 1rem;
        -moz-border-radius: 1rem;
        -ms-border-radius: 1rem;
        -o-border-radius: 1rem;
        display: flex;
        flex-flow: column nowrap;
        justify-content: center;
        margin-top: 6rem;
        margin-bottom: 3rem;
        border: 2px solid $light-gray;

        @include respond (phone) {
            & {
                width: 80%;
                padding: 2rem 2rem;
            }
        }
        
        & form {
            display: flex;
            flex-flow: column nowrap;
            width: 80%;
            margin: auto;
            justify-content: center;

            & .form__first-line {
                width: 90%;
                display: flex;
                flex-flow: row wrap;
                justify-content: space-between;

                @include respond (tab-land) {
                    & {
                        width: 100%;
                    }
                }

                &__name, 
                &__email {
                    width: 40%;
                    display: flex;
                    flex-flow: column wrap;

                    @include respond (tab-land) {
                        & {
                            width: 100%;
                        }
                    }
                }
            } 

            & .form__second-line {
                width: 90%;
                display: flex;
                flex-flow: row wrap;
                justify-content: space-between;
                
                @include respond (tab-land) {
                    & {
                        width: 100%;
                    }
                }

                &__phone, 
                &__subject {
                    width: 40%;
                    display: flex;
                    flex-flow: column wrap;

                    @include respond (tab-land) {
                        & {
                            width: 100%;
                        }
                    }
                }
            }

            & input,
            & textarea {
                margin-bottom: 3rem;
                border: 2px solid transparent;
                color: $app-background;
                font-size: 1.8rem;
                background: $light-gray;

                @include respond (tab-land) {
                    & {
                        margin-bottom: 1.5rem;
                    }
                }
            }

            & input.invalid,
            & textarea.invalid {
                border: 2px solid rgb(252, 89, 89);
            } 

            & label {
                text-align: left;
                font-weight: 500;
                margin-bottom: 1.5rem;

                @include respond (tab-land) {
                    & {
                        margin-bottom: 1rem;
                    }
                }
            }

            & input {
                height: 2.5rem;
                border-radius: 2.5rem;
                padding-left: 1rem;
                -webkit-border-radius: 2.5rem;
                -moz-border-radius: 2.5rem;
                -ms-border-radius: 2.5rem;
                -o-border-radius: 2.5rem;

                &:focus {
                    outline: none;
                    border: 2px solid $app-background;
                }
            }

            & textarea {
                border-radius: .5rem;
                -webkit-border-radius: .5rem;
                -moz-border-radius: .5rem;
                -ms-border-radius: .5rem;
                -o-border-radius: .5rem;
                
                &:focus {
                    outline: none;
                    border: 2px solid $app-background;
                }
            }
        }
    }
}